
<template>

  <div class="grid-container">
    <div class="grid-x">
      <h2>AFSPRAAK</h2>
    </div>

    <form v-if="!formIsHidden">
      <div class="grid-container">

        <div class="grid-x grid-padding-x" style="height: 45px;">
          <div class="small-4 medium-2 large-2 cell">
            <span>Datum:</span>
          </div>
          <div class="small-7 medium-5 large-4 cell">
            <span>{{ selected.date }}</span>
          </div>
        </div>

        <div class="grid-x grid-padding-x" style="height: 45px;">
          <div class="small-4 medium-2 large-2 cell">
            <span>Tijdslot:</span>
          </div>
          <div class="small-7 medium-5 large-4 cell">
            <span>{{ selected.start }} - {{ selected.end }}</span>
          </div>
        </div>

        <div class="grid-x grid-padding-x" style="height: 45px;">
          <div class="small-4 medium-2 large-2 cell">
            <span>Naam:</span>
          </div>
          <div class="small-7 medium-5 large-4 cell">
            <input v-model="naam" v-bind:class="{ 'is-invalid-input': nameHasError }" class="input-group-field" type="text">
          </div>
        </div>

        <div class="grid-x grid-padding-x" style="height: 45px;">
          <div class="small-4 medium-2 large-2 cell">
            <span>Email:</span>
          </div>
          <div class="small-7 medium-5 large-4 cell">
            <input v-model="email" v-bind:class="{ 'is-invalid-input': emailHasError }" class="input-group-field" type="email">
          </div>
        </div>

        <hr/>

        <div class="grid-x grid-padding-x" style="height: 45px;">
          <div class="small-4 medium-3 large-2 cell small-offset-3 medium-offset-1 large-offset-2">
            <a class="alert button small expanded" v-on:click="formIsHidden = true">Terug</a>
          </div>
          <div class="small-4 medium-3 large-2 cell">
            <a class="button small expanded" v-on:click="submitForm()">Bevestig</a>
          </div>
        </div>

      </div>
    </form>
    <div class="grid-container" v-if="formIsHidden">
      <div class="grid-x">
        <div class="cell small-12 medium-12 large-7">
          <table class="hover">
            <thead>
              <tr>
                <th>Datum</th>
                <th>Tijdslot</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              <tr class="tableRow" v-bind:class="{ booked: isBooked == event.id }" v-for="event in events" v-bind:key="event.id">
                <td>{{ event.date }}</td>
                <td>{{ event.start }} - {{ event.end }}</td>
                <td>
                  <a v-if="event.status == 0" class="button small expanded" v-on:click="handleForm(event.id)">Beschikbaar</a>
                  <a v-else class="disabled button small expanded">Bezet</a>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="cell small-12 medium-12 large-7">
            Stapsgewijs voegen we meer datums en tijden toe, naar hoe goed het met Robin gaat. Voor vragen graag mailen naar bamooijekind@gmail.com.
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>

  import { RepositoryFactory } from './users/RepositoryFactory'
  const UsersRepository = RepositoryFactory.get('users')

  export default {
    name: 'Afspraak',
    watch: {
      formIsHidden: function(val) {
        if (val == true) {
          this.naam = null
          this.nameHasError = false
          this.email = null
          this.emailHasError = false
          this.selected = null
        }
      }
    },
    mounted() {
      this.fetching().then(
        response => (this.info = response)
      )
    },
    methods: {
      formatDateTime(date) {
        return (
          date.getFullYear() +
          '-' +
          this.appendLeadingZeroes(date.getMonth() + 1) +
          '-' +
          this.appendLeadingZeroes(date.getDate()) +
          ' ' +
          this.appendLeadingZeroes(date.getHours()) +
          ':' +
          this.appendLeadingZeroes(date.getMinutes())
        )
      },
      formatTime(date) {
        return this.appendLeadingZeroes(date.getHours()) + ':' + this.appendLeadingZeroes(date.getMinutes())
      },
      formatDate(date) {
        return (
          date.getFullYear() +
          '-' +
          this.appendLeadingZeroes(date.getMonth() + 1) +
          '-' +
          this.appendLeadingZeroes(date.getDate())
        )
      },
      appendLeadingZeroes(n) {
        if (n <= 9) {
          return '0' + n
        }
        return n
      },
      sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
      },
      marshal(data) {
        var objects = []
        var i
        for (i = 0; i < data.length; i++) {
          var startDate = data[i]['start']['S'].replace(" ", "T") + "Z"
          var endDate = data[i]['end']['S'].replace(" ", "T") + "Z"
          var start = this.formatTime(new Date(Date.parse(startDate)))
          var end = this.formatTime(new Date(Date.parse(endDate)))

          var object = {
            date: this.formatDate(new Date(Date.parse(startDate))),
            email: data[i]['email']['S'],
            name: data[i]['name']['S'],
            end: end,
            status: parseInt(data[i]['status']['S']),
            id: parseInt(data[i]['userId']['S']),
            start: start
          }
          objects.push(object)
        }

        objects.sort((a, b) => {
          if (new Date(Date.parse(a.date + 'T' + a.start + "Z")) > new Date(Date.parse(b.date + 'T' + b.start + "Z"))) {
            return 1
          } else if (new Date(Date.parse(a.date + 'T' + a.start + "Z")) < Date.parse(b.date + 'T' + b.start + "Z")) {
            return -1
          } else {
            return 0
          }
        })
        return objects
      },
      async fetching() {
        this.isLoading = true

        UsersRepository.getAll().then(
          response => {
            this.events = this.marshal(response.data)
          },
          error => {
            console.log(error)
          }
        )
        this.isLoading = false
      },
      async store(data) {
        this.isLoading = true
        UsersRepository.create(data).then(
          response => {
            this.isBooked = data['id']
            console.log(response.data)
          },
          error => {
            console.log(error)
          }
        )
        this.isLoading = false
      },
      validateEmail(email) {
        return email.match(this.reg)
      },
      submitForm() {
        var index = this.events.findIndex(event => event.id === this.selected.id)

        if (this.naam == null) {
          this.nameHasError = 'is-invalid-input'
        } else {
          this.nameHasError = null
        }
        if (this.email == null || this.validateEmail(this.email) == null) {
          this.emailHasError = 'is-invalid-input'
        } else {
          this.emailHasError = null
        }
        if (this.emailHasError != null || this.nameHasError != null) {
          return
        }

        this.events[index].naam = this.naam
        this.events[index].email = this.email
        this.events[index].status = 1

        this.store(this.events[index])

        this.formIsHidden = true
      },
      handleForm(id) {
        var selected = this.events.find(event => event.id === id)
        if (selected.status == 0) {
          this.formIsHidden = false
          this.selected = selected
        }
      }
    },
    data: () => ({
      isBooked: null,
      object: [],
      naam: null,
      nameHasError: false,
      email: null,
      emailHasError: false,
      selected: null,
      formIsHidden: true,
      reg: /^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
      events: []
    })
  }

</script>

<style scope>

  .booked {
    border: 1px solid rgba(67, 172, 106, 0.5);
    background-color: rgba(67, 172, 106, 0.2) !important;
  }
  .button {
    background-color: rgba(77, 102, 102, 1) !important;
    margin: 0px;
    font-size: 20px;
  }
  .alert {
    background-color: rgba(102, 83, 77, 1) !important;
  }
  .tableRow {
    line-height: 30px;
  }

</style>
