import Repository from "./Repository";

const resource = "/users";

export default {
  getOne(user_id) {
    var url = `${resource}` + "/" + user_id
    return Repository.get(url);
  },
  getAll() {
    var url = `${resource}`
    return Repository.get(url);
  },
  remove(user_id) {
    var url = `${resource}` + "/" + user_id
    return Repository.delete(url);
  },
  create(user) {
    var url = `${resource}`
    var start = new Date(Date.parse(user["date"] + "T" + user["start"] + "Z"))
    start.setHours(start.getHours() - 4);
    var end = new Date(Date.parse(user["date"] + "T" + user["end"] + "Z"))
    end.setHours(end.getHours() - 4);

    const rbody = {
      userId: user["id"].toString(),
      email: user["email"],
      name: user["naam"],
      start: this.formatDateTime(start),
      end: this.formatDateTime(end),
      status: user["status"].toString()
    }
    return Repository.post(url, rbody);
  },
  formatDateTime(date) {
    return (
      date.getFullYear() +
      '-' +
      this.appendLeadingZeroes(date.getMonth() + 1) +
      '-' +
      this.appendLeadingZeroes(date.getDate()) +
      ' ' +
      this.appendLeadingZeroes(date.getHours()) +
      ':' +
      this.appendLeadingZeroes(date.getMinutes())
    )
  },
  formatTime(date) {
    return this.appendLeadingZeroes(date.getHours()) + ':' + this.appendLeadingZeroes(date.getMinutes())
  },
  formatDate(date) {
    return (
      date.getFullYear() +
      '-' +
      this.appendLeadingZeroes(date.getMonth() + 1) +
      '-' +
      this.appendLeadingZeroes(date.getDate())
    )
  },
  appendLeadingZeroes(n) {
    if (n <= 9) {
      return '0' + n
    }
    return n
  }
}
